import { DocumentEnhancerOptions, EnhancedRpJsonDocument } from "@rp/react/rpjson/types"
import { RpJsonDocument } from "@rp/react/rpjson/types"

const memo: Record<string, EnhancedRpJsonDocument> = {}

export const getDocumentEnhancerMemo = (
  rpjson: RpJsonDocument,
  options: DocumentEnhancerOptions = {},
): EnhancedRpJsonDocument => {
  const documentId = rpjson.document.rp_document_id
  return memo[`${documentId}-${JSON.stringify(options)}`]
}

export const setDocumentEnhancerMemo = (
  rpjson: RpJsonDocument,
  options: DocumentEnhancerOptions = {},
  value: EnhancedRpJsonDocument,
) => {
  const documentId = rpjson.document.rp_document_id
  memo[`${documentId}-${JSON.stringify(options)}`] = value
}
